import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { getLanguage } from '@wfp/freerice-core/modules/app';
import './FacebookButton.scss';

import strings from 'res/strings';

const UseFacebookToLoginButton = ({ language, tabIndex, history }) => {
  const useFacebookToLoginBtnClass = language === 'ar' ? 'use-facebook-to-login-btn-rtl' : 'use-facebook-to-login-btn';

  return (
    <div
      tabIndex={tabIndex}
      onKeyPress={() => history.push('/profile-use-facebook-to-login', { focusAfter: true })}
    >
      <Link
        tabIndex={-1}
        to="/profile-use-facebook-to-login"
        className={`${useFacebookToLoginBtnClass} ${useFacebookToLoginBtnClass}--icon`}
      >
        {strings['login.button.useFacebookToLogin']}
      </Link>
    </div>
  );
};

UseFacebookToLoginButton.propTypes = {
  language: PropTypes.string,
  tabIndex: PropTypes.number,
  history: PropTypes.object.isRequired,
};

UseFacebookToLoginButton.defaultProps = {
  language: strings.getLanguage(),
  tabIndex: 0,
};

export default withRouter(connect((state) => ({
  language: getLanguage(state),
}))(UseFacebookToLoginButton));
