import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

const AdImage = ({ imageUrl, target }) => {
  useEffect(() => {
    if (typeof window.gtag === 'function') {
      window.gtag('event', 'ad_shows', { target, imageUrl });
    }
  }, [target, imageUrl]);

  const handleClick = () => {
    if (typeof window.gtag === 'function') {
      window.gtag('event', 'ad_clicks', { target, imageUrl });
    }
  };

  return (
    <>
      {target ? (
        <a href={target} onClick={handleClick} target="_blank" rel="noopener noreferrer">
          <img
            src={imageUrl}
            title={imageUrl}
            alt={imageUrl}
          />
        </a>
      ) : (
        <img
          src={imageUrl}
          title={imageUrl}
          alt={imageUrl}
        />
      )}
    </>
  );
};

AdImage.propTypes = {
  imageUrl: PropTypes.string.isRequired,
  target: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
};

AdImage.defaultProps = {
  target: '',
};

export default AdImage;
