import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';
import withPageTitle from 'containers/common/withPageTitle';
import { withTabOrder, tabOrders, TAB_NAMES } from 'components/app/TabOrder';

import strings from 'res/strings';
import AgeScreenContent from 'components/pages/age-screen/AgeScreenContent';

const AgeScreen = ({ setTabOrder, focusTo }) => {
  const { state } = useLocation();

  useEffect(() => {
    setTabOrder(tabOrders.AGE_SCREEN);

    if (state && state.focusAfter) focusTo(TAB_NAMES.MENU_TOGGLE);
  }, []);

  return (
    <AgeScreenContent />
  );
};

AgeScreen.propTypes = {
  setTabOrder: PropTypes.func.isRequired,
  focusTo: PropTypes.func.isRequired,
};

export default withTabOrder(withPageTitle({ title: strings.age_screen_link })(AgeScreen));
