import React from 'react';
import PropTypes from 'prop-types';

const AdFrame = ({ file }) => (
  <iframe
    title={file}
    src={`/assets/ads/${file}?${(+new Date()).toString(36)}`}
    width="100%"
    height="100%"
    frameBorder="0"
    scrolling="no"
  />
);

AdFrame.propTypes = {
  file: PropTypes.string.isRequired,
};

export default AdFrame;
