import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';
import UseFacebookToLoginContent from 'components/pages/use-facebook-to-login/UseFacebookToLoginContent';
import withPageTitle from 'containers/common/withPageTitle';
import strings from 'res/strings';
import { withTabOrder, tabOrders, TAB_NAMES } from 'components/app/TabOrder';

const UseFacebookToLogin = ({ history, setTabOrder, focusTo }) => {
  const { state } = useLocation();

  useEffect(() => {
    setTabOrder(tabOrders.USE_FACEBOOK_TO_LOGIN);

    if (state && state.focusAfter) focusTo(TAB_NAMES.MENU_TOGGLE);
  }, []);

  return (
    <UseFacebookToLoginContent history={history} />
  );
};

UseFacebookToLogin.propTypes = {
  setTabOrder: PropTypes.func.isRequired,
  focusTo: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

export default withTabOrder(withPageTitle({ title: strings.use_facebook_to_login_link })(UseFacebookToLogin));
