// Check OIL config version. If none is found, use default.
// const oilConfig = process.env.NODE_ENV === 'test' ? '{}' : document.querySelector('#oil-configuration').innerHTML;
// const oilVersion = oilConfig ? JSON.parse(oilConfig).config_version : 1;

const knowIsDesktop = () => Math.max(document.documentElement.clientWidth, window.innerWidth || 0) >= 768;

const getAdFiles = ({ isCorrectAnswer, adList, isDesktop }) => {
  const searchCorrectAds = isDesktop ? 'right_300x250' : 'right_320x50';
  const searchIncorrectAds = isDesktop ? 'wrong_300x250' : 'wrong_320x50';
  const correctAds = adList.filter(ad => ad.category === searchCorrectAds);
  const incorrectAds = adList.filter(ad => ad.category === searchIncorrectAds);
  const currentArr = isCorrectAnswer ? correctAds : incorrectAds;
  const ad = sample(currentArr);
  const adSecond = sample(currentArr);

  // let adLocation;
  // let adLocationSecond;
  // if (window.localStorage.getItem('user-age') && window.localStorage.getItem('user-age') >= 16) {
  // if (isCorrectAnswer) {
  //   adLocation = { file: !isDesktop ? CORRECT_MOBILE : DESKTOP_CORRECT_RECTANGLE }; // CORRECT_DESKTOP_SMALL_RECT;
  //   adLocationSecond = isDesktop ? { file: DESKTOP_CORRECT_RECTANGLE } : null; // CORRECT_DESKTOP_LEADERBOARD;
  // } else {
  const adLocation = ad ? { imageUrl: ad.image, target: ad.target } : null;
  const adLocationSecond = (isDesktop && adSecond) ? { imageUrl: adSecond.image, target: adSecond.target } : null;
  // }
  // } else {
  //   adLocation = ad ? { imageUrl: ad.image, target: ad.target } : null;
  //   adLocationSecond = (isDesktop && adSecond) ? { imageUrl: adSecond.image, target: adSecond.target } : null;
  // }
  return { adLocation, adLocationSecond };
};

// We set two locations for desktop utilization, because we must use fixed size on css-side:
// the first one has small rectangle size, the second one leaderboard size.
// Mobile uses always the first one, so the second one will be always empty in small breakpoint.
// The default desktop location is the second one, because internal ads have leadearboard size.
const AdBlock = ({ isCorrectAnswer = false, tabOrder, adList }) => {
  const [adLocation, setAdLocation] = useState(null);
  const [adLocationSecond, setAdLocationSecond] = useState(null);
  const [isDesktop, setIsDesktop] = useState(knowIsDesktop());

  useEffect(() => {
    const handleResize = () => {
      setIsDesktop(knowIsDesktop());
    };
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (process.env.NODE_ENV !== 'test' && adList.length) {
      const { adLocation, adLocationSecond } = getAdFiles({ isCorrectAnswer, adList, isDesktop });
      setAdLocation(adLocation);
      setAdLocationSecond(adLocationSecond);
    }
  }, [adList, isDesktop]);

  return (
    <div id="advertisement" className="advertisement">
      {/* <div> */}
      {/* <BlockedAdMessage /> */}
      {!!adLocation && (
        <div tabIndex={tabOrder.ADVERTISEMENT_1} className="advertisement__first" title="Advertisement">
          {adLocation.imageUrl ? <AdImage {...adLocation} /> : <AdFrame file={adLocation.file} />}
        </div>
      )}
      {!!adLocationSecond && (!!adLocationSecond.imageUrl || !!adLocationSecond.file) && (
        <div tabIndex={tabOrder.ADVERTISEMENT_2} className="advertisement__second" title="Advertisement">
          {adLocationSecond.imageUrl ? <AdImage {...adLocationSecond} /> : <AdFrame file={adLocationSecond.file} />}
        </div>
      )}
      {/* </div> */}
    </div>
  );
};

AdBlock.propTypes = {
  isCorrectAnswer: PropTypes.bool.isRequired,
  tabOrder: PropTypes.object.isRequired,
  adList: PropTypes.array.isRequired,
};

export default connect(
  createStructuredSelector({
    adList: getAdList,
  }),
)(React.memo(withTabOrder(AdBlock)));
