/* eslint-disable max-len */
import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getLanguage } from '@wfp/freerice-core/modules/app';
import { getUserAccount, loginWithFacebook } from '@wfp/freerice-core/modules/user';
import { withTabOrder } from 'components/app/TabOrder';
import BoxInfo from 'components/pages/common/box/BoxInfo';

import FacebookButton from 'components/pages/profile/login/FacebookButton';

import './UseFacebookToLoginContent.scss';

import strings from 'res/strings';

const UseFacebookToLoginContent = ({ user, history, language, tabOrder, loginWithFacebook }) => {
  const boxStepClass = language === 'ar' ? 'box-step-rtl' : 'box-step';

  useEffect(() => {
    if (user && user.uuid) history.push('/profile-edit');
  }, [user]);

  const onResponseFacebook = response => {
    loginWithFacebook(response, language);
  };

  return (
    <BoxInfo type={`medium-width ${boxStepClass}`}>
      <p>
        {strings['usefacebooktologin.web.info.part1']}
      </p>
      <p>
        <b>{strings['usefacebooktologin.web.info.part2']}</b>
        {' '}
        {strings['usefacebooktologin.web.info.part3']}
      </p>
      <p>
        <b>{strings['usefacebooktologin.web.info.part4']}</b>
        {' '}
        {strings['usefacebooktologin.web.info.part5']}
      </p>
      <p>
        {strings['usefacebooktologin.web.info.part6']}
      </p>

      <br />
      <FacebookButton tabIndex={tabOrder.LOGIN_FACEBOOK_BUTTON} onResponseFacebook={onResponseFacebook} />
    </BoxInfo>
  );
};

UseFacebookToLoginContent.propTypes = {
  user: PropTypes.shape({
    uuid: PropTypes.string,
  }),
  language: PropTypes.string,
  tabOrder: PropTypes.object.isRequired,
  loginWithFacebook: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

UseFacebookToLoginContent.defaultProps = {
  user: null,
  language: strings.getLanguage(),
};

export default withTabOrder(connect((state) => ({
  language: getLanguage(state),
  user: getUserAccount(state),
}), { loginWithFacebook })(UseFacebookToLoginContent));
