import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getLanguage } from '@wfp/freerice-core/modules/app';
import './FacebookButton.scss';
import FacebookLogin from 'react-facebook-login';

import strings from 'res/strings';

const FacebookButton = ({ onResponseFacebook, language, tabIndex }) => {
  const facebookBtnClass = language === 'ar' ? 'facebook-btn-rtl' : 'facebook-btn';

  return (
    <div
      tabIndex={tabIndex}
      onKeyPress={(e) => e.target.getElementsByClassName(facebookBtnClass)[0].click()}
    >
      <FacebookLogin
        appId={process.env.REACT_APP_FACEBOOK_APP_ID}
        cssClass={`${facebookBtnClass} ${facebookBtnClass}--icon link--facebook-login`}
        returnScopes
        disableMobileRedirect
        callback={onResponseFacebook}
        tag="a"
        textButton={strings['login.button.facebook']}
        fields="name,email"
      />
    </div>
  );
};

FacebookButton.propTypes = {
  onResponseFacebook: PropTypes.func.isRequired,
  language: PropTypes.string,
  tabIndex: PropTypes.number,
};

FacebookButton.defaultProps = {
  language: strings.getLanguage(),
  tabIndex: 0,
};

export default connect((state) => ({
  language: getLanguage(state),
}))(FacebookButton);
