/* eslint-disable max-len */
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import DatePicker from 'react-datepicker';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import moment from 'moment';
import { getLanguage } from '@wfp/freerice-core/modules/app';
import { withTabOrder } from 'components/app/TabOrder';
import BoxInfo from 'components/pages/common/box/BoxInfo';
import Button from 'components/pages/common/Button';
import BoxInfoError from 'components/pages/common/box/BoxInfoError/BoxInfoError';

import './AgeScreenContent.scss';
import 'react-datepicker/dist/react-datepicker.css';

import strings from 'res/strings';

const AgeScreenContent = ({ language, tabOrder }) => {
  const history = useHistory();
  const [burnDate, setBurnDate] = useState(new Date());
  const [error, serError] = useState('');
  const ageScreenClass = language === 'ar' ? 'age-screen-rtl' : 'age-screen';

  const saveButtonHandler = () => {
    const userAge = moment().diff(burnDate, 'years');

    if (!Number.isNaN(userAge)) {
      window.localStorage.setItem('user-age', userAge);
      history.goBack();
    } else {
      serError(strings['ageVerif.web.age.incorrectDate']);
    }
  };

  return (
    <BoxInfo type={`medium-width ${ageScreenClass}`}>
      <p>{strings['onboarding3.new.text']}</p>
      <p>
        {strings['ageVerif.age.title']}
:
      </p>
      <DatePicker
        showYearDropdown
        scrollableYearDropdown
        yearDropdownItemNumber={60}
        dateFormatCalendar="MMMM"
        maxDate={new Date()}
        dateFormat="MMMM dd, yyyy"
        selected={burnDate}
        onChange={date => serError('') || setBurnDate(date)}
      />
      {!!error && <BoxInfoError messages={[error]} />}
      <br />
      <Button
        tabIndex={tabOrder.SAVE_BUTTON}
        className={`${ageScreenClass}-save-button`}
        color="green"
        text={strings['common.save']}
        handlerClick={saveButtonHandler}
      />
    </BoxInfo>
  );
};

AgeScreenContent.propTypes = {
  language: PropTypes.string,
  tabOrder: PropTypes.object.isRequired,
};

AgeScreenContent.defaultProps = {
  language: strings.getLanguage(),
};

export default withTabOrder(connect((state) => ({
  language: getLanguage(state),
}))(AgeScreenContent));
